import { PATCH } from './patch'; //REORDER_INSERT

// åˆ†åˆ«æ‰¾åˆ°æœ‰ key çš„å…ƒç´ ä½ç½®å’Œæ²¡æœ‰ key çš„å…ƒç´ çš„ä½ç½®
function makeKeyIndexAndFree (list) {
  const keyIndex = {}; // æœ‰ key çš„èŠ‚ç‚¹ä½ç½®
  const free = []; // å¯æ›¿æ¢çš„ä½ç½®ï¼ˆæ²¡æœ‰ key çš„èŠ‚ç‚¹éƒ½è¢«æ ‡è¯†ä¸ºå¯æ›¿æ¢çš„èŠ‚ç‚¹ï¼‰
  for (let i = 0, len = list.length; i < len; i++) {
    const item = list[i]
    const itemKey = item.key;
    if (itemKey) {
      keyIndex[itemKey] = i;
    } else {
      free.push(item);
    }
  }
  return {
    keyIndex: keyIndex,
    free: free
  };
}

export default function listDiff (pList, nList) {
  const nMap = makeKeyIndexAndFree(nList);
  const nKeys = nMap.keyIndex, nFree = nMap.free;
  const pMap = makeKeyIndexAndFree(pList);
  const pKeys = pMap.keyIndex, pFree = pMap.free;
  // å…ˆå¤„ç†æœ‰ key çš„å…ƒç´ ï¼Œçœ‹å…¶åœ¨ nList è¿˜æ˜¯å¦å­˜åœ¨ï¼Œå¦‚æœä¸å­˜åœ¨è¯´æ˜è¢«ç§»é™¤
  const children = [];
  for (let i = 0, freeIndex = 0; i < pList.length; ++i) {
    const item = pList[i];
    const itemKey = item.key;
    if (itemKey) {
      if (!nKeys.hasOwnProperty(itemKey)) {
        children.push(null);
      } else {
        const itemKeyIndex = nKeys[itemKey];
        children.push(nList[itemKeyIndex]);
      }
    } else {
      children.push(nFree[freeIndex++] || null);
    }
  }
  const moves = [];
  function remove (index) {
    moves.push({
      type: PATCH.REMOVE,
      index: index
    });
  }
  function insert (index, item) {
    moves.push({
      type: PATCH.INSERT,
      index: index,
      item: item
    });
  }
  const simulateList = children.slice(0);
  // æ‰¾å‡ºè¢«ç§»é™¤çš„èŠ‚ç‚¹
  let i = 0;
  while (i < simulateList.length) {
    if (simulateList[i] === null) {
      remove(i);
      simulateList.splice(i, 1);
    } else {
      ++i;
    }
  }
  // éå† nList
  for (let s = 0, n = 0; n < nList.length; ++n) {
    const nItem = nList[n];
    const nItemKey = nItem.key;
    const sItem = simulateList[s];
    if (sItem) {
      const sItemKey = sItem.key;
      if (sItemKey === nItemKey) { // ç›¸åŒå…ƒç´ ç›¸åŒä½ç½®
        s++;
      } else {
        if (!pKeys.hasOwnProperty(nItemKey)) { // æ—§åˆ—è¡¨ä¸­ä¸å­˜åœ¨ï¼Œæ–°èŠ‚ç‚¹
          insert(n, nItem); // åœ¨ä½ç½® n æ’å…¥æ–°èŠ‚ç‚¹ nItem
        } else { // æ—§åˆ—è¡¨ä¸­å­˜åœ¨ï¼Œéœ€è¦ç§»ä½ï¼ˆç§»ä½æ“ä½œåŒ…æ‹¬åˆ é™¤å’Œæ’å…¥ä¸¤è€…ä¸­çš„ä¸€ä¸ªæˆ–ä¸¤ä¸ªï¼‰
          const nsItemKey = simulateList[s + 1].key;
          if (nsItemKey === nItemKey) {
            remove(n);
            simulateList.splice(s, 1);
            s++;
          } else {
            insert(n, nItem);
          }
        }
      }
    } else { // æ—§åˆ—è¡¨è¯¥ä½ç½®ä¸ºç©ºï¼Œç›´æ¥æ’å…¥
      insert(n, nItem);
    }
  }
  return {
    moves: moves,
    children: children
  };
}
