import { setAttr, replaceNode, removeNode } from '../dom';

export const PATCH = {
  REPLACE: 0, // æ›¿æ¢èŠ‚ç‚¹
  INSERT: 1, // æ’å…¥
  REMOVE: 2, // ç§»é™¤
  REORDER: 3, // é‡æ’
  PROPS: 4, // ä¿®æ”¹å±æ€§
  TEXT: 5 // æ›¿æ¢æ–‡æœ¬
};

// @TODO patch çš„ index ä¸å¯¹ï¼Ÿ
export function patch (context, domNode, patches) {
  const walker = {index: 0};
  dfsWalk(context, domNode, walker, patches);
}

function dfsWalk (context, domNode, walker, patches) {
  const currentPatches = patches[walker.index];

  const len = domNode.childNodes ? domNode.childNodes.length : 0;
  for (let i = 0; i < len; i++) {
    const child = domNode.childNodes[i];
    walker.index++;
    dfsWalk(context, child, walker, patches);
  }
  if (currentPatches) {
    applyPatches(context, domNode, currentPatches);
  }
}

function patchReorder (context, domNode, moves) {
  for (let i = 0; i < moves.length; ++i) {
    const move = moves[i];
    switch (move.type) {
      case PATCH.INSERT: // æ’å…¥æ–°èŠ‚ç‚¹
        const target = domNode.childNodes[move.index] || null; // null æ’å…¥æœ«å°¾
        domNode.insertBefore(move.item.render(context), target);
        break;
      case PATCH.REMOVE:
        removeNode(domNode.childNodes[move.index]);
        break;
      default:
        // error type
    }
  }
}

// æ£€æŸ¥æ˜¯å¦æŒ‡ä»¤å±æ€§
function isAttrDirective (attr) {
  return /^@|n-|:/.test(attr);
}
// æ£€æŸ¥æ˜¯å¦äº‹ä»¶æŒ‡ä»¤
function isEventDirective (attr) {
  return /^@/.test(attr);
}

function patchProps (domNode, patch) {
  for (let p in patch.props) {
    if (patch.props.hasOwnProperty(p)) {
      // æ£€æŸ¥æ˜¯å¦æŒ‡ä»¤å±æ€§
      if (isAttrDirective(p)) {
        if (isEventDirective(p)) {
          // removeEventListener
          // addEventListener
        } else { // å…¶ä»–æŒ‡ä»¤å±æ€§
        }
      } else { // æ™®é€šå±æ€§
        setAttr(domNode, p, patch.props[p]);
      }
    }
  }
}

// æ ¹æ®è¡¥ä¸æ›´æ–° DOM èŠ‚ç‚¹
function applyPatches (context, domNode, patches) {
  for (let i = 0; i < patches.length; ++i) {
    const patch = patches[i];
    switch (patch.type) {
      case PATCH.REPLACE: // æ›¿æ¢å…ƒç´ èŠ‚ç‚¹
        replaceNode(patch.node.render(context), domNode);
        break;
      case PATCH.PROPS: // å±æ€§ä¿®æ”¹
        patchProps(domNode, patch);
        break;
      case PATCH.TEXT: // æ›¿æ¢æ–‡æœ¬å†…å®¹
        domNode.data = patch.data;
        break;
      case PATCH.REORDER: // å­èŠ‚ç‚¹é‡æ–°æ’åº
        patchReorder(context, domNode, patch.moves);
        break;
      default:
        // warn
    }
  }
}
